export default class Booking extends Component {

    constructor() {
        super()

        this.state = {
            date1: new Date(),
            date2: new Date(),
            userName: "",
            userEmail: "",
            inputToDate: "",
            inputFromDate: "",
            adultCount: 0,
            childCount: 0,
            infantCount: 0,
            userMessage: "",
            feedbackMsg: "",
            feedbackMsgColor: "#EC1629"

        }

        this.increaseAdult = this.increaseAdult.bind(this)
        this.decreaseAdult = this.decreaseAdult.bind(this)
        this.increaseChild = this.increaseChild.bind(this)
        this.decreaseChild = this.decreaseChild.bind(this)
        this.increaseInfant = this.increaseInfant.bind(this)
        this.decreaseInfant = this.decreaseInfant.bind(this)
        this.pushtoDatabase = this.pushtoDatabase.bind(this)
        this.updateName = this.updateName.bind(this)
        this.updateMessage = this.updateMessage.bind(this)
        
    }


    
  
  
    onChangeFromDate = (date1) => {
        this.setState({ date1, inputFromDate: this.state.date1}) 
        console.log(this.state.date1)
    }

    onChangeToDate = (date2) => {
        this.setState({date2, inputToDate: this.state.date2})
    }

    increaseAdult () {
        this.setState({ adultCount: this.state.adultCount +=1 })
    }

    decreaseAdult () {
        if (this.state.adultCount > 0) {
            this.setState({ adultCount: this.state.adultCount -=1 })
        }
    }

    increaseChild () {
        this.setState({ childCount: this.state.childCount +=1 })
    }

    decreaseChild () {
        if (this.state.childCount > 0) {
            this.setState({ childCount: this.state.childCount-=1 })
        }
    }

    increaseInfant () {
        this.setState({ infantCount: this.state.infantCount +=1 })
    }

    decreaseInfant () {
        if (this.state.infantCount > 0) {
            this.setState({ infantCount: this.state.infantCount -=1 })
        }
    }

    updateName = event => {
        this.setState({ [event.target.id]: event.target.value }) 
        this.setState({ userName: event.target.value})
    }

    updateEmail = event => {
        this.setState({ [event.target.id]: event.target.value }) 
        this.setState({ userEmail: event.target.value})
    }

    updateMessage = event => {
        this.setState({ [event.target.id]: event.target.value }) 
        this.setState({ userMessage: event.target.value })
    }

    pushtoDatabase () {
        var db = firebase.firestore()
        if (this.state.userName != "" && this.state.userEmail != "" && this.state.inputFromDate != "" && this.state.inputToDate != "") {
            db.collection("reservations").doc("sleeps").collection("bookings").add({
                name: this.state.userName,
                email: this.state.userEmail,
                checkin: this.state.inputFromDate,
                checkout: this.state.inputToDate,
                adults: this.state.adultCount,
                children: this.state.childCount,
                infants: this.state.infantCount,
                message: this.state.userMessage
            })
            this.setState({
                feedbackMsg: "Thanks! We've received your booking information and we'll get back to you soon!",
                feedbackMsgColor: "#42BF29",
                userName: "",
                userEmail: "",
                inputFromDate: "",
                inputToDate: "",
                childCount: 0,
                adultCount: 0,
                infantCount: 0
            })
        } else {
            this.setState({
                feedbackMsg: "Oops. Looks like there's some missing information, please review your details.",
            })
        } 
    }


    render () {


        const feedbackMsgStyle = {
            textAlign: "center",
            color: this.state.feedbackMsgColor,
            fontFamily: "Fredoka One"
        }


        return (
            <Styles>
                <div className="backBtn">
                    <button
                    onClick={event => window.location.href="/select"}
                    >
                        <figure><img src="assets/back.png"></img></figure>
                        <figcaption>Back</figcaption>
                    </button>
                </div>
                <div className="header">
                    <h1><u>Book Accomodation</u></h1>
                    <img src="assets/sleep_icon2.png"></img>
                </div>
                <div className="bookingForm">
                    <div className="userName">
                        <label><u>Name:</u></label>
                        <input
                        onChange={this.updateName}
                        className="userNameInput"
                        placeholder="Enter your full name"
                        value={this.state.userName}
                        ></input>
                    </div>
                    <div className="userEmail">
                        <label><u>Email:</u></label>
                        <input
                        onChange={this.updateEmail}
                        className="userEmailInput"
                        placeholder="Enter your email"
                        value={this.state.userEmail}
                        ></input>
                    </div>
                    <div className="fromDate">
                        <label><u>Check-In:</u></label>
                        <input
                        // onChange={this.updateInput}
                        className="fromDateInput"
                        placeholder="Choose a check-in date "
                        value={this.state.inputFromDate}
                        ></input>
                        <OverlayTrigger trigger="click" placement="right" overlay={
                            <Popover>
                            <Popover.Title as="h4">Double-click a date to select it</Popover.Title>
                            <Popover.Content>
                                <Calendar
                                    onChange={this.onChangeFromDate}
                                    value={this.state.date1}
                                />
                            {/* <strong>Holy guacamole!</strong> Check this info. */}
                            </Popover.Content>
                        </Popover>
                        }>
                            <button className="btn btn-primary">Select a Date</button>
                        </OverlayTrigger>
                    </div>
                    <div className="toDate">
                        <label><u>Checkout:</u></label>
                        <input
                        className="toDateInput"
                        placeholder="Choose a checkout date"
                        value = {this.state.inputToDate}
                        ></input>
                        <OverlayTrigger trigger="click" placement="right" overlay={
                            <Popover>
                            <Popover.Title as="h4">Double-click a date to select it</Popover.Title>
                            <Popover.Content>
                                <Calendar
                                    onChange={this.onChangeToDate}
                                    value={this.state.date2}
                                />
                            {/* <strong>Holy guacamole!</strong> Check this info. */}
                            </Popover.Content>
                        </Popover>
                        }>
                            <button className="btn btn-primary">Select a Date</button>
                        </OverlayTrigger>
                    </div>
                    <div className="guestCount">
                        <label className="guestLabel"><u>Guests:</u></label>

                        <label className="adultLabel">Adults</label>
                        <input
                        onChange={this.updateInput}
                        value={this.state.adultCount}
                        ></input>
                        <button 
                        className="adultUpBtn"
                        onClick={this.increaseAdult}
                        ><strong>↑</strong></button>
                        <button 
                        className="adultDownBtn"
                        onClick={this.decreaseAdult}
                        ><strong>↓</strong></button>


                        <label className="childLabel">Children</label>
                        <input
                        onChange={this.updateInput}
                        value={this.state.childCount}
                        ></input>
                        <button 
                        className="childUpBtn"
                        onClick={this.increaseChild}
                        ><strong>↑</strong></button>
                        <button 
                        className="childDownBtn"
                        onClick={this.decreaseChild}
                        ><strong>↓</strong></button>


                        <label className="infantLabel">Infants</label>
                        <input
                        onChange={this.updateInput}
                        value={this.state.infantCount}
                        ></input>
                        <button 
                        className="infantUpBtn"
                        onClick={this.increaseInfant}
                        ><strong>↑</strong></button>
                        <button 
                        className="infantDownBtn"
                        onClick={this.decreaseInfant}
                        ><strong>↓</strong></button>
                    </div>
                    <div className="userMessage">
                        <p className="userLabel"><u>Leave us a short message (if you'd like) ! </u></p>
                        <textarea
                        onChange={this.updateMessage}
                        value={this.state.userMessage} 
                        className="userTextarea"
                        placeholder="Enter a message for us here (optional)"
                        ></textarea>
                    </div>
                    <div className="feedbackMsg">
                        <h5
                        style={feedbackMsgStyle}
                        >{this.state.feedbackMsg}</h5>
                    </div>
                    <div className="submitButton">
                        <button 
                        onClick={this.pushtoDatabase}
                        className="btn btn-primary">
                            Complete Booking
                        </button>
                    </div>
                </div> 

            </Styles>
            
            
        )
    }
}