export default class Event extends Component {
    constructor() {
        super()

        this.state = {
            date: new Date(),
            userName: "",
            userEmail: "",
            userMessage:"",
            inputFromDate: "",
            feedbackMsg: "",
            feedbackMsgColor: "#EC1629",
            weddingClick: false,
            preweddingClick: false,
            receptionClick: false,
            graduationClick: false,
            lunchClick: false,
            otherClick: false,
            otherEvent: ""

        }

        this.onChangeFromDate = this.onChangeFromDate.bind(this)
        this.updateName = this.updateName.bind(this)
        this.updateEmail = this.updateEmail.bind(this)
        this.updateMessage = this.updateMessage.bind(this)
        this.updateOtherEvent = this.updateOtherEvent.bind(this)
        this.handlePrewedding = this.handlePrewedding.bind(this)
        this.handleWedding = this.handleWedding.bind(this)
        this.handleReception = this.handleReception.bind(this)
        this.handleGraduation = this.handleGraduation.bind(this)
        this.handleLuncheon = this.handleLuncheon.bind(this)
        this.handleOther = this.handleOther.bind(this)
        this.pushtoDatabase = this.pushtoDatabase.bind(this)
    }


    onChangeFromDate = (date) => {
        this.setState({ date, inputFromDate: this.state.date}) 
    }

    updateName = event => {
        this.setState({ [event.target.id]: event.target.value }) 
        this.setState({ userName: event.target.value})
    }

    updateEmail = event => {
        this.setState({ [event.target.id]: event.target.value }) 
        this.setState({ userEmail: event.target.value})
    }

    updateMessage = event => {
        this.setState({ [event.target.id]: event.target.value }) 
        this.setState({ userMessage: event.target.value })
    }

    updateOtherEvent = event => {
        this.setState({ [event.target.id]: event.target.value }) 
        this.setState({ otherEvent: event.target.value })
    }


    handlePrewedding (event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({preweddingClick: value})
    }

    handleWedding (event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({weddingClick: value})
    }


    handleReception (event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({receptionClick: value})
    }

    handleGraduation (event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({graduationClick: value})
    }

    handleLuncheon (event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({lunchClick: value})
    }

    handleOther (event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({otherClick: value})
    }


    pushtoDatabase () {
        var db = firebase.firestore()
        if (this.state.userName != "" && this.state.userEmail != "" && this.state.inputFromDate != "") {
            db.collection("reservations").doc("events").collection("bookings").add({
                name: this.state.userName,
                email: this.state.userEmail,
                date: this.state.inputFromDate,
                message: this.state.userMessage,
                prewedding: this.state.preweddingClick,
                wedding: this.state.weddingClick,
                reception: this.state.receptionClick,
                graduation: this.state.graduationClick,
                lunch: this.state.lunchClick,
                other: this.state.otherClick,
                otherEvent: this.state.otherEvent 
            })
            this.setState({
                feedbackMsg: "Thanks! We've received your booking information and we'll get back to you soon!",
                feedbackMsgColor: "#42BF29",
                userName: "",
                userEmail: "",
                userMessage: "",
                inputFromDate: "",
                graduationClick: false,
                preweddingClick: false,
                weddingClick: false,
                receptionClick: false,
                lunchClick: false,
                otherClick: false,
                otherEvent: ""
            })
        } else {
            this.setState({
                feedbackMsg: "Oops. Looks like there's some missing information, please review your details.",
                feedbackMsgColor: "#EC1629"
            })
        } 
    }



    render () {

        const feedbackMsgStyle = {
            textAlign: "center",
            color: this.state.feedbackMsgColor,
            fontFamily: "Fredoka One"
        }


        return (
            <Styles>
                <div className="backBtn">
                    <button
                    onClick={event => window.location.href="/select"}
                    >
                        <figure><img src="assets/back.png"></img></figure>
                        <figcaption>Back</figcaption>
                    </button>
                </div>
                <div className="header">
                    <h1><u>Book an Event</u></h1>
                    <img src="assets/events_icon2.png"></img>
                </div>
                <div className="userName">
                    <label><u>Name:</u></label>
                    <input
                    onChange={this.updateName}
                    className="userNameInput"
                    placeholder="Enter your full name"
                    value={this.state.userName}
                    ></input>
                </div>
                <div className="userEmail">
                    <label><u>Email:</u></label>
                    <input
                    onChange={this.updateEmail}
                    className="userEmailInput"
                    placeholder="Enter your email here"
                    value={this.state.userEmail}
                    ></input>
                </div>
                <div className="fromDate">
                        <label><u>Date:</u></label>
                        <input
                        // onChange={this.updateInput}
                        className="fromDateInput"
                        placeholder="Choose a date "
                        value={this.state.inputFromDate}
                        ></input>
                        <OverlayTrigger trigger="click" placement="right" overlay={
                            <Popover>
                            <Popover.Title as="h4">Double-click a date to select it</Popover.Title>
                            <Popover.Content>
                                <Calendar
                                    onChange={this.onChangeFromDate}
                                    value={this.state.date}
                                />
                            {/* <strong>Holy guacamole!</strong> Check this info. */}
                            </Popover.Content>
                        </Popover>
                        }>
                            <button className="btn btn-primary">Select a Date</button>
                        </OverlayTrigger>
                </div>
                <div className="eventSelection">
                    <label className="selection"><u>Select an event(s):</u></label>
                    <br></br>
                    <label className="eventOption">Pre-wedding Ceremony</label>
                    <input
                    name="Prewedding"
                    type="checkbox"
                    checked={this.state.preweddingClick}
                    // onClick={this.handlePrewedding()}
                    ></input>

                    <br></br>
                    <label className="eventWedding">Wedding Ceremony</label>
                    <input
                    name="Wedding"
                    type="checkbox"
                    // checked={this.state.isTrue}
                    onClick={this.handleInputChange}
                    ></input>

                    <br></br>
                    <label className="eventReception">Reception Ceremony</label>
                    <input
                    name="Reception"
                    type="checkbox"
                    checked={this.state.isTrue}
                    onChange={this.handleReception}
                    ></input>

                    <br></br>
                    <label className="eventGraduation">Graduation Ceremony</label>
                    <input
                    name="Graduation"
                    type="checkbox"
                    // checked={this.state.isTrue}
                    onChange={this.handleGraduation}
                    ></input>

                    <br></br>
                    <label className="eventLuncheon">Luncheon</label>
                    <input
                    name="Luncheon"
                    type="checkbox"
                    // checked={this.state.isTrue}
                    onChange={this.handleLuncheon}
                    ></input>

                    <br></br>
                    <label className="eventOther">Other</label>
                    <input
                    name="Other"
                    type="checkbox"
                    // checked={this.state.isTrue}
                    onChange={this.handleOther}
                    ></input>
                </div>
                <div className="otherEvent">
                    <label><u>Other:</u></label>
                    <input
                    onChange={this.updateOtherEvent}
                    className="otherEventInput"
                    placeholder="Tell us your event (if not listed)"
                    value={this.state.otherEvent}
                    ></input>
                </div>
                <div className="userMessage">
                        <p className="userLabel"><u>Leave us a short message (if you'd like) ! </u></p>
                        <textarea
                        onChange={this.updateMessage}
                        value={this.state.userMessage} 
                        className="userTextarea"
                        placeholder="Enter a message for us here (optional)"
                        ></textarea>
                    </div>
                    <div className="feedbackMsg">
                        <h5
                        style={feedbackMsgStyle}
                        >{this.state.feedbackMsg}</h5>
                    </div>
                    <div className="submitButton">
                        <button 
                        onClick={this.pushtoDatabase}
                        className="btn btn-primary">
                            Complete Booking
                        </button>
                </div>

            </Styles>
        )
    }
}